.article{
  &__box {
    max-width: 495px;
    text-align: center;
    font-size: 18px;
    line-height: 1.944;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $textColor;
    padding-top: 60px;
  }
  
  &__quote {
    z-index: 1;
    transform: translateY(-7px);
    line-height: 1.95;
  }
  
  &__cite {
    transform: translate(-16px, 5px);
    color: #f5ae3e;
  }
  
  &__img {
    // transform: translate(0, -100px);
    z-index: 1;
    margin-left: 25px;
  }
}